import React ,{useState , Component} from "react";
import { Link } from "gatsby";


import { IoIosArrowRoundForward } from "react-icons/io";
// import { Container, Row, Col } from "reusecore/Layout";

import PageHeader from "reusecore/PageHeader";
import Sidebar from "sections/Blog-sidebar";




import { Flex , Heading ,useMediaQuery, createBreakpoints , Button , Box , SimpleGrid ,Text ,Select , Input , Center , Divider ,Image ,extendTheme  } from "@chakra-ui/react"
import logo from "assets/images/app/logo.png"



class PosCal extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
   
      enteredValues: {
        accountBalance: props.accountBalance,
        riskperecentage : props.riskperecentage,
        pips:props.pips,
      
        
       
      }
     
    }
  }

  changeText = (position_size) => {

    this.setState({ position_size }); 
  } 

  changeText2 = (amount_risk) => {

    this.setState({ amount_risk }); 
  } 





  handleFirstNameChanged(event) {
    var enteredValues        = this.state.enteredValues;
    enteredValues.accountBalance  = event.target.value;
    this.setState({ enteredValues: enteredValues });

    
  }

  handlerisk(event) {
    var enteredValues        = this.state.enteredValues;
  

    enteredValues.riskperecentage  = event.target.value;

    this.setState({ enteredValues: enteredValues });

    
  }



  handlespips(event) {
    var enteredValues   = this.state.enteredValues;
    enteredValues.pips  = event.target.value;

    this.setState({ enteredValues: enteredValues });   
  }



  

  handleButtonClicked() {

  
    // console.log("working");

    const account_balance = parseInt(this.state.enteredValues.accountBalance)
    const risk_perecentage = parseInt(this.state.enteredValues.riskperecentage)
    const stop_loss =parseInt(this.state.enteredValues.pips)

    this.setState({ test: "Updated Content!"});

    const positionSize = account_balance*risk_perecentage/stop_loss
    const amountAtRisk = account_balance*(risk_perecentage/100)
  

    // console.log("working");
    console.log( "Position Size "+ positionSize);

    this.changeText(positionSize)
    this.changeText2(amountAtRisk)


  }

  render() {

    
    const { position_size } = this.state
    const { amount_risk } = this.state


    return(

    
      
        
      <Box   width="100%" height="900" bg="#00468b">

      



      <Image src={logo} alt="Segun Adebayo"  width="200px"  

      objectFit="cover"/>  

      {/* <img src={logo} alt="prime app landing" />  */}

      <Box    height={{
      base: "100%", // 0-48em
      md: "50%", // 48em-80em,
      xl: "25%", // 80em+
    }}  marginBottom={0} margin={100} bg="transparent" w="auto" marginTop={0} >

         <Text color="white" paddingTop={40} fontWeight="bold" fontSize={50} align="center"  border={3}>  Position Size Calculator </Text>
    
      <Box bg="#00003f" w="70%" p={4} color="white" height="auto" marginTop={100}  marginX="15%"	 borderRadius={25} border="none" box-shadow="md" marginBottom={0} >


              

                  <SimpleGrid columns={3} spacing={0} padding={15}>

                  
                          <Box bg="transparent"   height="350px" alignItems="v" margin="auto">

                          <form method = "post">

                          <Text fontSize="22">Account Currency </Text>

                          <Select placeholder="Select" height={35} borderRadius={25} width="60%" border="none" marginBottom={30} fontSize="18"  >
                              <option value="AUD">AUD</option>
                              <option value="CAD"> CAD</option>
                              <option value="BGP"> BGP</option>
                          </Select>

                          <Text fontSize="22">Account Balance </Text>

                          <Input height={40} placeholder="0"  value={this.state.enteredValues.accountBalance}  onChange={this.handleFirstNameChanged.bind(this)} type="text"  maxWidth={170} borderRadius={25} border="none" marginBottom={30} fontSize="18" />

                          <Text fontSize="22"> Currency Pair </Text>

                          <Select placeholder="Currency Pair" height={40} borderRadius={25} width={45} border="none" fontSize="18" >
                              <option value="GBP/USD">GBP/USD</option>
                              <option value="USD/CHF">USD/CHF</option>
                              <option value="USD/CAD">USD/CAD</option>
                          </Select>

                          </form>

                          </Box>

                          <Box bg="transparent" height="350px"  margin="auto">

                          <form method ="post">
                              
                          <Text fontSize="22">Risk Precentage </Text>

                          <Input height={40} type="number"  value={this.state.enteredValues.riskperecentage}  onChange={this.handlerisk.bind(this)}  placeholder="0" width={130} borderRadius={25} border="none" marginBottom={30} fontSize={18} />



                          <Text fontSize="22">Stop Loss (pips) </Text>

                          <Input height={40}  type='number'  value={this.state.enteredValues.pips}  onChange={this.handlespips.bind(this)}  placeholder="0" width={130} borderRadius={25} border="none" marginBottom={60} fontSize={18} />


                          <Text fontSize="2"></Text>

                          <Button borderRadius={25} width={130} height={40} colorScheme="blue" fontSize={18}  onClick={this.handleButtonClicked.bind(this)}  >Calculate</Button>

                          </form>


                          </Box>

                    
                          <Box borderRadius={20} bg="#000058" height="auto" alignItems="center" boxShadow="md" p="6" width="auto" >


                          <Text marginTop={20} align="center" padding={0} alignSelf="center" fontWeight="bold" fontSize="25"> Results </Text>

                          <Text  marginLeft={20} fontSize="18" marginBottom={0} >Amount At Risk </Text>


                          <Text     marginX="70%" fontSize="18" marginBottom={0}>{amount_risk}</Text>


                          <Divider marginBottom={25}  marginTop={0} bg="white" height={3} color="black" size={3} marginLeft={20} marginRight={20} width="80%"/>

                          <Text  marginLeft={20} fontSize="18" marginBottom={0} >Position Size ( Units ) </Text>


                          <Text marginX="70%" fontSize="18" marginBottom={0}>{position_size}</Text>



                          <Divider marginBottom={25}  marginTop={0} bg="white" height={3} color="black" size={3} marginLeft={20} marginRight={20} width="80%"/>


                          <Text  marginLeft={20} fontSize="18" marginBottom={0} >Standard Lots </Text>


                          <Text  marginX="70%" fontSize="18" marginBottom={0}></Text>
                          <Divider marginBottom={25}  marginTop={0} bg="white" height={3} color="black" size={3} marginLeft={20} marginRight={20} width="80%"/>


                          <Text  marginLeft={20} fontSize="18" marginBottom={0} >Mini Lots </Text>
                          <Text  marginX="70%" fontSize="18" marginBottom={0}></Text>
                          <Divider marginBottom={25}  marginTop={0} bg="white" height={3} color="black" size={3} marginLeft={20} marginRight={20} width="80%"/>


                          <Text  marginLeft={20} fontSize="18" marginBottom={0} >Micro Lots </Text>
                          <Text  marginX="70%" fontSize="18" marginBottom={0}></Text>
                          <Divider marginBottom={25}  marginTop={0} bg="white" height={3} color="black" size={3} marginLeft={20} marginRight={20} width="80%"/>

                       
                          </Box>

                  
                
                  </SimpleGrid>
      </Box>
      

      </Box>


      </Box>

    );



  }
}


export default PosCal







